.project-card{
    background:#130f2a;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 1.5rem;
    margin: 0 1rem;
    margin-top: 80px;
}

.project-card h6{
    font-size: 1.65rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.project-technologies{
    display: inline-block;
    font-size: 0.7rem;
    font-weight: 400;
    background: rgba(103,81,185,0.5);
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
}

.project-card ul li{
    list-style: none;
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.project-card ul li::after{
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    background-color: #6751b9;
    border-radius: 0.5rem;
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}

@media (max-width:768px){
    .project-card{
        margin: 0;
    }
    .project-card h6{
        font-size: 0.95rem;
    }

    .project-card ul li{
        font-size: 0.75rem;
    }
}

